/**
 * 
 * @param {string} name        
 * @param {string} albumname   
 * @param {string} albumdesc   
 */
export const formatSinger = (name, albumname, albumdesc) => {
  albumname = albumname == '' ? '' : `  ${albumname}`;
  albumdesc = albumdesc == '' ? '' : `  ${albumdesc}`;
  return `${name}${albumname}${albumdesc}`;
}


