import url from './api'
import jsonp from './jsonp'

/**
 *  API 
 */
class API {
  constructor ({url, jsonp}) {
    let newObj = {};
    this.url = url;
    this.jsonp = jsonp;

    Object.keys(url).forEach(item => {
      newObj[item] = jsonp.bind(null, url[item]);
    })

    // 
    Object.assign(this, newObj);

  }
}

export default new API({url, jsonp})


